








import {Component, Prop, Vue} from 'vue-property-decorator';

@Component({
    name: 'ftb-button',
    props: {
        disabled: Boolean,
        click: Function,
        color: String,
        cssClass:  String,
        noPadding: Boolean,
        isRounded: {
          type: Boolean,
          default: true,
        },
    },
})
export default class FTBButton extends Vue {
  @Prop({default: false})
  private disabled!: boolean;
  private handleClick(){
    if(this.disabled){
      return;
    }
    this.$emit('click')
  }
}
