























import {Component, Prop, Vue } from 'vue-property-decorator';
import {Action, State} from 'vuex-class';
import os from 'os';
import { SettingsState } from '../modules/settings/types';

@Component
export default class TitleBar extends Vue {
    @Action('sendMessage') public sendMessage: any;
    @Action('disconnect') public disconnect: any;
    @State('settings') private settings!: SettingsState;
    @Action('saveSettings', {namespace: 'settings'}) private saveSettings!: any;
    public isMac = false;

    private windowId: string | null = null;

    public mounted() {
        if (os.type() === 'Darwin') {
            this.isMac = true;
        } else {
            this.isMac = false;
        }
        //@ts-ignore
        overwolf.windows.getCurrentWindow((result) => {
          if(result && result.status === "success" && result.window && result.window.id){
            this.windowId = result.window.id;
          }
        });
    }

    public getWindowState(){
      return new Promise((resolve, reject) => {
        //@ts-ignore
        overwolf.windows.getWindowState(this.windowId, (res) => {
          if(res && res.status === "success"){
            resolve(res.window_state_ex);
          } else {
            reject(res);
          }
        })
      });
    }

    public startDragging(event: any){
      if(event.target.classList.contains("title-action")){
        return;
      }
      //@ts-ignore
      overwolf.windows.dragMove(this.windowId)
    }

    // @ts-ignore
    public close(): void {
        //@ts-ignore
        overwolf.windows.close(this.windowId);
        //@ts-ignore
        if(window.isChat === undefined || window.isChat === false){
            this.saveSettings(this.settings?.settings);
            this.disconnect();
            //@ts-ignore
            // overwolf.windows.getMainWindow().closeEverythingAndLeave();
        }
    }

    // @ts-ignore
    public minimise(): void {
        //@ts-ignore
        overwolf.windows.minimize(this.windowId);
        //@ts-ignore
        if(window.ad){
          //@ts-ignore
          window.ad.removeAd()
        }
    }

    // @ts-ignore
    public async max(): void {
        let maximised = await this.getWindowState();
        if(maximised === "maximized"){
          //@ts-ignore
          overwolf.windows.restore(this.windowId);
          //@ts-ignore
          if(window.ad){
            //@ts-ignore
            window.ad.refreshAd();
          }
        } else {
          //@ts-ignore
          overwolf.windows.maximize(this.windowId);
        }
    }
}
