























import { Component, Prop, Vue } from 'vue-property-decorator';
import NavItem from './nav/NavItem.vue';
// import {shell, ipcRenderer} from 'electron';
import {AuthState} from '../modules/auth/types';
import {State, Action} from 'vuex-class';
import store from '@/store';
import config from '@/config';
import { SettingsState } from '../modules/settings/types';
import { logVerbose } from '../utils';
import { ModpackState } from '../modules/modpacks/types';

@Component({components: {NavItem}})
export default class Sidebar extends Vue {
  @State('auth') private auth!: AuthState;
  @State('modpacks') private modpacks!: ModpackState;
  @State('settings') private settings!: SettingsState;
  @Action('setSessionID', {namespace: 'auth'}) private setSessionID!: any;
  @Action('saveSettings', {namespace: 'settings'}) private saveSettings!: any;
  private appVersion: string = config.appVersion;


  get isDevelop() {
    const splits = this.appVersion.split('-');
    if (splits.length === 0) {
      return true;
    }
    return !splits[splits.length - 1].match(/\d/);
  }


  get disableNav(){
    return this.$router.currentRoute.path.startsWith("/launching") || (this.modpacks.installing !== null && !this.modpacks.installing.error);
  }

  get avatarName(){
    const provider = this.auth.token?.accounts.find((s) => s.identityProvider === 'mcauth');
    return provider !== undefined && provider !== null ? provider.userId : "MHF_Steve";
  }


  public isActiveTab(tab: string): boolean {
    return tab === 'home' && this.$route.path === '/' ? true : this.$route.path.startsWith(`/${tab}`);
  }

  public goTo(page: string): void {
    if(this.disableNav){
      return;
    }
    // We don't care about this error!
    this.$router.push({name: page}).catch((err) => { return; });
  }

  public openPromo(): void {
    //@ts-ignore
    overwolf.utils.openUrlInDefaultBrowser('https://creeperhost.net/applyPromo/FEEDME');
  }

  public openFTB(): void {
    //@ts-ignore
    overwolf.utils.openUrlInDefaultBrowser('https://feed-the-beast.com');
  }

  public openGithub(): void {
    //@ts-ignore
    overwolf.utils.openUrlInDefaultBrowser('https://github.com/FTBTeam/FTB-App-Overwolf/issues');
  }

  public openFriends() {
    //@ts-ignore
    overwolf.windows.obtainDeclaredWindow('chat', function(result){
      if(result.success){
        if(!result.window.isVisible){
    //@ts-ignore
          overwolf.windows.restore(result.window.id);
        }
      }
    });
  }

  private async openLogin() {
    //@ts-ignore
    let r = await overwolf.windows.getMainWindow().openWebserver((data) => {
        if(data.token){
          this.setSessionID(data.token);
        }
        if(data['app-auth']){
          let settings = this.settings?.settings;
          if(settings !== undefined){
              settings.sessionString = data['app-auth'];
          }
          this.saveSettings(settings);
        }
    });
    //@ts-ignore
    overwolf.utils.openUrlInDefaultBrowser(`https://minetogether.io/api/login?redirect=http://localhost:7755`);
  }

  private debugLog(data: any) {
    logVerbose(this.settings, data);
  }
}
