





























import { Component, Prop, Vue } from 'vue-property-decorator';
@Component({
    props: [
        'label',
        'maxValue',
        'minValue',
        'currentValue',
        'value',
        'unit',
        'maxValueLabel',
        'step',
        'description',
        'cssClass',
        'rawStyle',
    ],
})
export default class FTBSlider extends Vue {
    @Prop({default: ''})
    public label!: string;
    @Prop({default: 100})
    public maxValue!: number;
    @Prop({default: 0})
    public minValue!: number;
    @Prop({default: 0})
    public currentValue!: number;
    @Prop({default: ''})
    public unit!: string;
    @Prop({default: ''})
    public description!: string;
    @Prop({default: ''})
    public cssClass!: string;
    @Prop({default: ''})
    public rawStyle!: string;
}
